@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    margin: 0;
    padding: 0;
}
html, body, textarea {
    font-family: 'Roboto Mono', monospace;
}

body {
    background-image: -webkit-gradient(linear, left bottom, right top, from(#1879d8), color-stop(#00a6ef), color-stop(#00cac7), color-stop(#00e375), to(#dceb12));
    background-image: -webkit-linear-gradient(left bottom, #1879d8, #00a6ef, #00cac7, #00e375, #dceb12);
    background-image: linear-gradient(to right top, #1879d8, #00a6ef, #00cac7, #00e375, #dceb12);
    background-size: cover;
    min-height: 100vh;
}

.App {
    text-align: center;
}

